<template>
	<div class="yd-input">
		<input :type="type" :value="value"  @input="$emit('input',$event.target.value)"/>
		<span>{{text}}</span>
	</div>
</template>

<script>
	export default {
		name: 'yd-input',
		props: {
			text:{
				type:String,
			},
			type:{
				type:String,
				default:'text'
			},
			value:{
				type:String,
				default:''
			}
		}
	}
</script>

<style scoped>
	.yd-input {
		border-bottom: 1px solid #e5e7e8;
		height:0.95rem; position:relative;
	}

	.yd-input input {
		padding-left: 1.24rem;
		width: 4.54rem;
		height: 100%;
		font-size: 0.4rem;
	}

	.yd-input span {
		width: 1.23rem;
		height: 0.49rem;
		position: absolute;
		top: 0.26rem;
		left: 0;
		border-right: 1px solid #676868;
		color: #676868;
		font-size: 0.25rem;
		line-height: 0.49rem;
		text-align: center;
	}
</style>
